
@import url(//fonts.googleapis.com/earlyaccess/notosanstc.css);
@import url(//fonts.googleapis.com/earlyaccess/cwtexhei.css);
@import url(//fonts.googleapis.com/earlyaccess/cwtexyen.css);
*{box-sizing:border-box
}
body{font-family:Roboto,"cwTeXYen","Heiti TC",sans-serif;font-size:2vmin;padding:0;margin:0;visibility:visible;opacity:1;transition:opacity 0.5s ease
}
h1,h2,h3,h4,p{font-family:Roboto,"cwTeXYen","Heiti TC",sans-serif;color:rgba(71,71,71,0.9)
}
.fade-enter,.fade-leave-active{opacity:0
}
.fade-leave-active,.fade-enter-active{transition:opacity .3s ease
}
.component{position:relative
}
.component h1,.component h2,.component h3,.component h4,.component h5,.component h6,.component p{font-family:Roboto,"cwTeXYen","Heiti TC",sans-serif
}
.component h1 a,.component h2 a,.component h3 a,.component h4 a,.component h5 a,.component h6 a,.component p a{cursor:pointer !important;color:dimgray;border-bottom:1px dashed lightgray
}
.component strong{font-weight:900;color:black
}
@media only screen and (max-width: 767px){
.fat-only{display:none !important
}
}
.fat-only p{font-size:1.8rem
}
@media only screen and (min-width: 768px){
.thin-only{display:none !important
}
}
.thin-only p{font-size:1.5rem
}



